let result = require('./sudoku.js') ;
let init = require('./initData.js') ;

function initFn(_this){
	_this.hw();
	window.onresize = function(){
	  _this.hw();
	}
}

function optionFn(a,b,c,d,_this){
  //选出点击小方框的坐标位置
  _this.x = a*3 + c;
  _this.y = b*3 + d;
  _this.clickFlg = true;
  setTimeout(function(){_this.hw();},200);
}

//填入数值的函数
function fillFn(fillVal,_this){
  _this.clickFlg = false;
  var x = _this.x;
  var y = _this.y;
  _this.sd[x][y] = fillVal;
}

function handle(index,_this){
  if(index == 0){//答案
    solve(_this)
  }
  else if(index == 1){//重置
    reset(_this)
  }
  else{//求解
    commit(_this)
  }
}

function solve(_this){
  reset(_this);
  let obj = result.data(_this.sd);
  console.log('结果得：',obj.bool);
  if(obj.bool){
    _this.sd = obj.data[0];
  }
}
function reset(_this){
  //二维数组的深度拷贝
  _this.sd1 = init.data[_this.value];
  var arr = [];
  for(let i=0; i<9; i++){
    arr.push(_this.sd1[i].slice(0));
  }
  _this.sd = arr;
}

function commit(_this){
  let f = true;
  _this.sd.forEach((val => {
    if(val.includes(0)){
      f = false;
    }
  }));
  if(!f){
    _this.$notify({
      title: '警告',
      message: '还有空格没有填哦！',
      type: 'warning'
    });
    return ;
  }
  let obj = result.data(_this.sd);
  if(obj.bool){
    //成功后的提示框
    if(_this.value < init.options.length-1){
      _this.$notify({
        title: '胜利',
        message: '你很棒！！',
        type: 'success'
      });
      _this.value++;
    }else{
      _this.$notify({
        title: '王者至尊',
        message: '恭喜你到达巅峰了！',
        type: 'success'
      });
    }
  }else{
    //失败后的提示框
    _this.$notify.error({
      title: '失败',
      message: '很遗憾...'
    });
  }
}

function zidiyi(_this){
  let arr = [],str = [];
  let bool = false;
  str = _this.initText.split('\n');
  str.forEach(val => {
    arr.push(val.split(''));
  });

  bool = result.data(arr).bool; 
  if(!bool){
    _this.$notify({
      title: '导入失败',
      message: '初始值不符合规则！',
      type: 'error'
    });
    return ;
  }else{
    init.data[0] = arr;
    reset(_this); 
    _this.$notify({
      title: '导入成功',
      message: '准备开始游戏！',
      type: 'success'
    });   
  }
}

module.exports = {
	initFn,
	optionFn,
	fillFn,
	handle,
	reset,
  zidiyi
}


