<template>
  <div>  
    <div class="box">
      <el-row :gutter="2" type="flex" justify="center" v-for="q in len" :key="q" class="margin_top_2">
        <el-col :xs="8" :sm="4" :md="4" :lg="4" :xl="4" v-for="i in len" :key="i" :class="bgc[q][i]">
          <el-row  v-for="j in len" :key="j">
            <el-col :span="8" v-for="k in len" :key="k">
              <div class="border_1" :class="disab[parseInt(sd1[q*3+j][i*3+k])?0:1]" @click="optionFn(q,i,j,k)">
              <img class="img" :src="'./static/'+imgs[sd[q*3+j][i*3+k]]">
            </div>
            </el-col>
          </el-row>
        </el-col>

        <!-- 大于手机屏幕时显示的部分 -->
        <el-col :span="4" class="hidden-xs-only" type="flex" justify="center">
          <el-button type="primary" plain @click="handle(q)">{{btn[q]}}</el-button>

          <!-- 提供1-9填数的弹出框 -->
          <el-dialog
            :visible.sync="clickFlg"
            :modal="false"
            width="20%"
            center>
            <el-row v-for="ii in len" :key="ii">
              <el-col :span="8" v-for="jj in len" :key="jj">
                <div class="border_1 border_2" @click="fillFn(ii*3 + jj + 1)">
                  <img class="img" :src="'./static/'+imgs[ii*3 + jj + 1]">
                </div>
              </el-col>
            </el-row>
          </el-dialog>

        </el-col>
      </el-row> 
    </div> 
    
    <div>
      <p><hr></p>
      <el-row type="flex" justify="center" :gutter="5">
        <el-col :span="7">
          <el-select size="small" v-model="value">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="8" v-if="zidiyi">

          <el-dropdown trigger="click">
            <el-button size="small" round class="el-dropdown-link">
              定义初始值<i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
                <el-input
                  type="textarea"
                  :autosize="{ minRows: 9, maxRows: 9}"
                  placeholder="输入初始值
空位填'0'
一行数字直接相连
每行数字回车分开
(可用记事本写好再粘贴上来)"
                  v-model="initText">
                </el-input>
            </el-dropdown-menu>
          </el-dropdown>
        </el-col>

        <el-col :span="2" v-if="zidiyi">
          <el-button size="small" plain type="info" @click="initdata" round>导入</el-button>
        </el-col>
      </el-row>
      <p><hr></p>

      <!-- 手机上显示的部分 -->
      <el-row class="hidden-sm-and-up" :gutter="20">
        <el-col :span="8" v-for="(i,index) in btn" :key="i">
          <el-button plain type="primary" size="mini" @click="handle(index)" round>{{i}}</el-button>
        </el-col>

        <!-- 提供1-9填数的弹出框 -->
        <el-dialog
          :visible.sync="clickFlg"
          :modal="false"
          width="45%"
          center>
          <el-row v-for="ii in len" :key="ii">
            <el-col :span="8" v-for="jj in len" :key="jj">
              <div class="border_1 border_2" @click="fillFn(ii*3 + jj + 1)">
                <img class="img" :src="'./static/'+imgs[ii*3 + jj + 1]">
              </div>
            </el-col>
          </el-row>
        </el-dialog>

      </el-row>
    </div>

  </div>
</template>

<script>
import result from '../js/sudoku.js'
import init from '../js/initData.js'
import funs from '../js/funs.js'
export default {

  name: 'HelloWorld',
  data () {
    var hw = function(){
      var oh = document.querySelectorAll(".border_1");
      var height = oh[0].offsetWidth;  //获取每一个的宽度，然后一次设置高度
      for(let i=0; i<oh.length ; i++){
        oh[i].style.height = height + 'px';
      } 
    }
    return {
      hw:hw,    //设置宽高的函数
      x:0,
      y:0,
      value: 1,
      sd1:null,
      sd:null,
      clickFlg:false,
      zidiyi:false,
      initText:'',
      options: init.options,
      len:[0,1,2],
      disab:['disabled',''],
      btn:['答案','重置','求解'],
      bgc:[
        ['skyblue','pink','skyblue'],
        ['pink','skyblue','pink'],
        ['skyblue','pink','skyblue'],
      ],
      imgs:['img0.png','img1.png','img2.png','img3.png','img4.png','img5.png','img6.png','img7.png','img8.png','img9.png']
    }
  },
  created(){
    funs.reset(this);
  },
  mounted(){
    funs.initFn(this);
  },
  methods:{
    //点击填数方框函数
    optionFn(a,b,c,d){
      funs.optionFn(a,b,c,d,this);
    },
    //填入数值的函数
    fillFn(fillVal){
      funs.fillFn(fillVal,this);
    },
    handle(index){
      funs.handle(index,this);
    },
    initdata(){
      funs.zidiyi(this);
    }
  },
  watch:{
    value(){
      funs.reset(this);
      if(!this.value){
        this.zidiyi = true;
      }else{
        this.zidiyi = false;
      }
    }
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .border_1{
    border-color:#666;
    border-left: 1px solid;
    border-bottom: 1px solid;
    text-align: center;
    font-size: 100%;
    font-weight: bold;
    cursor: pointer;
  }
  .border_2{
    border-color:green;
    border-top: 1px solid;
    border-right: 1px solid;
  }
  .border_1:hover{
    background-color: yellow;
  }
 
  .margin_top_2{
    margin-top: 1px;
  }
  .pink{
    border: 1px solid green;
    background-color: pink;
  }
  .skyblue{
    border: 1px solid green;
    background-color: skyblue;
  }
  hr{
    width: 68%;
  }
  /* dom元素点击失效事件 */
 .disabled {
    pointer-events: none;
    cursor: default;
    background-color: rgba(26,95,205,0.2);
  }
  .img{
    width: 100%;
    height: 100%;
    display: block;
    margin: 0;
    padding: 0;
  }
  .span{
    border:1px solid blue;
    padding: 10px;
    cursor: pointer;
  }
</style>



