let list = [];
let f = null;
let initArr = null;
let count = 0;

function solveSudoku(sd){
	list = [];
	initArr = sd;
	f = false;
	count = 0;
	let str = "";
	str = initStr(0, 0, str);
	fun(0, 0, 0, 0, str, 0);
	return {data:list,bool:f};
}

//截取每块小九宫格的9个数值
function initStr(i, j, str) {
	for (let k = i; k < i + 3; k++) {
		for (let k2 = j; k2 < j + 3; k2++) {
			if (initArr[k][k2] != 0)
				str += initArr[k][k2] + "";
		}
	}
	return str;
}

function fun(lenV, id, x, y, str, num) {
	if(count >= 10)return;
	if (id == 3) {
		if (x == 8 && y == 9) {
			f = true;
			count++;
			result();
			return;

		}
		if (lenV == 8) {
			lenV = -1;
			x = -1;
			num += 3;
			if (num == 9)
				num = 0;
		}
		y = num;
		if (lenV + 1 == 0 || lenV + 1 == 3 || lenV + 1 == 6) {
			str = "";
			str = initStr(lenV + 1, y, str);
		}
		fun(lenV + 1, 0, x + 1, y, str, num);

	} else {
		if (initArr[x][y] != 0){
			if(judge_1(initArr[x][y], x, y)){
				fun(lenV, id + 1, x, y + 1, str, num);
			}
			else return;
		}
		else
			for (let i = 1; i < 10; i++) {
				let f = judge(i, x, y);
				if (f == false || str.includes("" + i))
					continue;
				initArr[x][y] = i;
				str += i + "";
				fun(lenV, id + 1, x, y + 1, str, num);
				initArr[x][y] = 0;
				str = str.substring(0, str.length - 1);
			}
	}

}

function result() {
	let arr = [];
	for (let i = 0; i < 9; i++) {
		let str = "";
		for (let j = 0; j < 9; j++) {
			str += initArr[i][j] + "";
		}
		arr.push(str.split(''));
	}
	list.push(arr);
}

function judge(i, x, y) {
	for (let j = 0; j < 9; j++) {
		if (i == initArr[x][j] || i == initArr[j][y])
			return false;
	}
	
	return true;
}

//判断该位置上的数值不在所在的小九宫格以及大九宫格的行列上有出现过
function judge_1(i, x, y) {
	let x1 = num(x);
	let y1 = num(y);
	for (let j = 0; j < 9; j++) {
		if((j != y) && (i == initArr[x][j])){
			return false;
		}
		if((j != x) && (i == initArr[j][y])){
			return false;
		}
	}
	for(let k1=x1; k1<x1+3;k1++){
		for(let k2=y1; k2<y1+3;k2++){
			if(k1==x && k2==y)continue;
			if(i == initArr[k1][k2]){
				return false;
			}
		}
	}
	return true;
}
//确定小九宫格的起始坐标
function num(par){
	let y = 0;
	if(par >= 6){
		y = 6;
	}else if(par >= 3){
		y = 3;
	}else{
		y = 0;
	}
	return y;
}

module.exports = {data: solveSudoku,bool:f};


